@import url(https://fonts.googleapis.com/css?family=Wire+One|Raleway:300);
body {
  margin: 0;
  padding:  0;
  font-family:  arial;
  /*background:  linear-gradient(to right, rgb(236, 202, 202), rgb(226, 116, 120));*/
  background-image: url(/portfolio/static/media/light.fe337d38.jpg);
  background-position:  center;
  background-repeat:  no-repeat;
  background-attachment: fixed;
  background-size:  cover;
  height: 1500px;
}


.photo {
	height: 100px;
}
.welcome {
	font-size: 5rem;
	color: black;
	font-family: "Raleway";
}



.scrolling-wrapper {
	display: flex;
	flex-wrap: nowrap;
	overflow-x:  auto;
	max-width: 1200px;
}

.title {
	font-family: 'Wire One';
	font-size: 80px;
	padding-bottom: 30px;
}

.scallop-down {
  height: 100px;
  width: 100%;
  background-image: radial-gradient(50px at 50% 0, #fff, #ffdfdf 49%, transparent 51%, transparent);
  background-size: 49px 100%;
}

.scallop-up {
  height:100px;
  width:100%;
  background-image: radial-gradient(50px at 50% 100%, #fff, #ffdfdf 49%, transparent 51%, transparent);
  background-size: 49px 100%;
}

.card {
	display: flex;
	flex: 0 0 auto;
	width: 400px;
	flex-grow: 4;
	padding: 40px 5px 10px 5px;
}

.bookImage {
	height: 300px;
	object-fit: contain;
}

h1, h4 {
	font-family: "Raleway";
}

.projectImage {
	height: 300px;
	object-fit: contain;
}
.card {
	width: 400px;	
	display: flex;
	flex-grow: 4;
}

.center {
	align-items: center;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

